  width: 100%;
  height: 100%;
  border-radius: 10px;
}

article {
  display: flex;
  flex-direction: column;
  font-family: "Open Sans", sans-serif;
  line-height: 40px;
  margin: 0 40px;
}
@media (min-width: 820px) {
  article {
    padding: 10px;
    margin: 0;
  }
}
article h2 {
  margin-top: 40px;
}
@media (min-width: 820px) and (max-width: 1024px) {
  article h2 {
    margin: 40px 20px 10px 20px;
  }
}
article p {
  margin-bottom: 20px;
}
@media (min-width: 820px) and (max-width: 1024px) {
  article p {
    margin: 0 20px 40px 20px;
  }
}

aside {
  font-family: "Open Sans", sans-serif;
}
aside div {
  line-height: 30px;
}
aside div h2 {
  margin: 30px 25px;
}
aside div p {
  margin: 40px 25px;
}
aside button {
  width: 100%;
  height: 60px;
  background-color: #1691C3;
  border-radius: 5px;
  color: white;
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 100px;
  position: relative;
}
aside button i {
  margin-right: 10px;
}
@media (max-width: 1024px) {
  aside button:after {
    content: "";
    width: 100%;
    height: 3px;
    position: absolute;
    bottom: -100px;
    left: 0;
    background-color: lightgray;
  }
}

.stuff .container .row .image {
  height: 150px;
}

.picture {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  padding: 5px;
}

.more-btn {
  margin-top: 30px;
}

.random {
  display: flex;
  flex-direction: column;
}
.random p {
  width: 70%;
}

.more-btn:after {
  display: none;
}

.info .container .row {
  position: relative;
}
@media (max-width: 1024px) {
  .info .container .row:after {
    content: "";
    width: 100%;
    height: 3px;
    position: absolute;
    bottom: 280px;
    left: 0;
    background-color: lightgray;
  }
}
.info .container .row .random {
  margin-bottom: 80px;
}
.info .container .row .random p {
  margin-top: 30px;
  width: 50%;
  text-decoration: underline;
  line-height: 35px;
}
.info .container .row .stuff4 .icons i {
  margin-top: 30px;
  padding: 3px;
  font-size: 40px;
}
.info .container .row .stuff4 p {
  margin-top: 5px;
}

.Stuff-header {
  margin: 20px 0;
}

@media (max-width: 1024px) {
  .stuff3, .stuff4 {
    display: none;
  }
}
@media (min-width: 1500px) {
  .main-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 1024px;
    padding: 0;
  }
  .main-section aside {
    order: 2;
  }
  .main-section aside .continue-btn {
    margin-top: -15px;
  }
  .main-section .stuff {
    order: 1;
  }
}
@media (min-width: 1500px) {
  .info .container {
    display: flex;
  }
  .info .container .row {
    width: 50%;
    display: flex;
  }
  .info .container .row .random p {
    width: 100%;
  }
}